import './donorCard.css'
function DonorCard() {
    return (  
        <div className='don_card'>
            <img src="./cat image.png" alt="" />
            <div className='card_middle'>
                <div className='blood_type'>
                    <p>O(I) RH-</p>
                </div>
                <p> 1 </p>
            </div>
            <div className='address'>
                <img src="./gps.png" alt="" />
                <p>., . , .8</p>
            </div>
        </div>
    );
}

export default DonorCard;