import { useState } from "react";
import Modal from "../../modules/Modal/Modal";
import "./header.css";
import logo from "../../../assets/icons/  2024-02-24 120639.svg";
const customStyles = {
  content: {
    padding: 0,
    margin: 0,
    width: "1000px",
    height: "500px",
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
function Header() {
  const [modalState, setModalState] = useState({
    isOpen: false,
    view: "",
  });
  const closeModal = () => {
    setModalState((state) => ({
      ...state,
      isOpen: false,
      view: "",
    }));
  };
  const changeView = (view) => {
    setModalState((state) => ({
      ...state,
      view,
    }));
  };
  return (
    <>
      <div className='header'>
        <div className='logo'>
          <img src={logo} alt='' />
        </div>
        <div className='unit'>
          <a className='link' href='#'>
             
          </a>
          <p>|</p>
          <a className='link' href='#'>
             
          </a>
          <p>|</p>
          <div className='dropdown'>
            <p className='dropbtn link'> </p>
            <div className='dropdown-content'>
              <a className='link' href='#'>
                 
              </a>
              <a className='link' href='#'>
                 
              </a>
              <a className='link' href='#'>
                    
              </a>
            </div>
          </div>
        </div>
        <div className='logIn'>
          <button
            className='link'
            onClick={() => {
              setModalState(() => ({
                isOpen: true,
                view: "log in",
              }));
            }}
          >
            
          </button>
          <p>/</p>
          <button
            className='link'
            onClick={() => {
              setModalState(() => ({
                isOpen: true,
                view: "register-2",
              }));
            }}
          >
            
          </button>
        </div>
      </div>
      <div className='headLine'></div>
      <Modal
        modalState={modalState}
        closeModal={closeModal}
        changeView={changeView}
        customStyles={customStyles}
      />
    </>
  );
}

export default Header;
